.contacts__map-wrapper.no-js .contacts__map {
  display: none;
}

.contacts__content {
  max-width: 500px;
  flex-grow: 1;
  align-self: center;
}
@media (max-width: 767px) {
  .contacts__content {
    margin-bottom: 43px;
  }
}

.contacts__content h3 {
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
}

.contacts__address {
  font-style: normal;
}

.contacts__tel-link:hover {
  opacity: 0.7;
  text-decoration: underline;
}
.contacts__tel-link:active {
  opacity: 0.5;
}

.contacts__address-item {
  position: relative;
  display: block;
  min-height: 22px;
  padding-left: 32px;
}
.contacts__address-item::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  display: block;
  width: 22px;
  height: 22px;
  background-position: center;
  background-repeat: no-repeat;
}
.contacts__address-item--pin::before {
  background-image: url("../img/svg/pin.svg");
  background-size: 18px 22px;
}
.contacts__address-item--tel::before {
  background-image: url("../img/svg/phone.svg");
  background-size: 18px 18px;
}

